@import url(https://fonts.googleapis.com/css?family=Merriweather&display=swap);
@import url(https://fonts.googleapis.com/css?family=Cabin&display=swap);
@import url(https://fonts.googleapis.com/css?family=Merriweather&display=swap);

.Todos {
  width: 300px;
    padding: 15px;

  color: black;
  border-left: solid #9C27B0 0.9px;
 box-shadow: 2px 2px 10px lightblue;
 background: aliceblue;
 overflow: hidden;
}
.Todos h1{
    margin: 15px;
}
.Todos div {
    color: white;
  margin: 5px 20px 10px 20px;
  padding: 10px 20px 10px 20px;
  box-shadow: 1px 1px 5px lightslategray ;
  border-radius: 4px;
  background: rgb(71, 39, 77);
  word-wrap: break-word;
-webkit-animation: animate 700ms;
        animation: animate 700ms;

  

}
@-webkit-keyframes animate{
  0%{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
  }
  100%{
    -webkit-transform: scale(1);
            transform: scale(1);
    
  }
}
@keyframes animate{
  0%{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
  }
  100%{
    -webkit-transform: scale(1);
            transform: scale(1);
    
  }
}
.InputAndTodo {
  display: flex;
  flex-direction: row;
  justify-content: justify;
width: 100%;
  background: #1abc9c;
  box-shadow: 2px 2px 10px lightblue;
  border-radius: 5px;
}
#Input {
    margin: 20px;
  width: 200px;
  height: 30px;
  border: none;
  border-radius: 4px;
  padding: 10px;
}
#btn {
  border-radius: 3px;
  border:none;

}
.iptbtn {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  width:100%;
  width: 300px;
  
  height: 300px;
  overflow: hidden;
}
.Edit, .Delete{
    font-family: 'Cabin', sans-serif;
    margin-left: 10px;
    padding: 0px;
    border: none;

   
}
@media(max-width:700px){
  .InputAndTodo {
    width: auto;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  background: #bcb91a;
  border-radius: 5px;
  overflow: hidden;
  margin: 5%;
}
.iptbtn,.Todos {


    width: 100%;
    /* background: aliceblue; */

}

}
